.botton-nav {
  position: relative;
  width: 60px;
  height: 60px;
  cursor: pointer;
  padding: 5px;
  transition: box-shadow 0.3s ease-in-out;
  background-color: transparent;
}
.botton-nav .line {
  height: 5px;
  background: #303030;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  transition: all 1s ease-in-out;
}
.botton-nav .line:before {
  content: "";
  position: absolute;
  height: 5px;
  width: 40px;
  top: 15px;
  z-index: 222;
  background: #303030;
  transition: all 1s ease-in-out;
  -webkit-transition: all 1s ease-in-out;
}
.botton-nav .line:after {
  content: "";
  position: absolute;
  height: 5px;
  width: 40px;
  bottom: 15px;
  z-index: 222;
  background: #303030;
  transition: all 1s ease-in-out;
  -webkit-transition: all 1s ease-in-out;
}

.botton-nav.active {
  background-color: #cecece;
}

.botton-nav.active > .line:before {
  right: 0;
  width: 40px;
}

.botton-nav.active > .line:after {
  right: 0;
